body {
  font-family: 'Karla';
  color: #525252;
  background-color: #f3efe0; }

.mainPanel {
  min-height: 100vh;
  display: grid;
  grid-template-rows: 100vh;
  grid-template-columns: auto 4fr; }

.sidebar {
  font-family: OpenSans, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  text-align: center;
  position: fixed;
  height: 100vh;
  background: black;
  color: white;
  margin: 0px;
  padding: 40px 50px 0 20px;
  grid-row: 1;
  grid-column: 1; }
  .sidebar ul {
    list-style-type: none;
    margin-top: 150px; }
    .sidebar ul li a {
      text-transform: uppercase;
      color: #e1e1e1;
      cursor: pointer;
      text-decoration: none;
      line-height: 40px;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 2px; }
    .sidebar ul li a:hover {
      color: #e17b77; }

.centralPanel {
  font-family: 'Karla';
  --font-family: 'Lucida Grande';
  --font-family: 'Roboto';
  --font-family: 'Helvetica';
  font-size: 17px;
  font-style: normal;
  font-feature-settings: normal;
  font-variant: normal;
  font-weight: 400;
  max-width: 900px;
  --line-height: 18.4px;
  background-attachment: scroll;
  color: #000000;
  padding: 0px 8px;
  margin: 0 auto 0 auto;
  grid-row: 1;
  grid-column: 2; }

.zoom-gallery {
  padding-bottom: 20px; }
  .zoom-gallery h3 {
    text-align: center;
    clear: both; }
  .zoom-gallery p {
    padding: 5px;
    clear: both; }
    .zoom-gallery p a img {
      margin: 10px; }

.ProjectsLearnMoreLink {
  color: inherit;
  text-decoration: none; }

.techDetailsSubHeader {
  font-style: italic;
  font-weight: 600;
  display: block;
  margin: 0; }

blockquote:before {
  position: relative;
  content: "\201C";
  display: block;
  width: 100%;
  text-align: center;
  font-size: 66px;
  font-family: '';
  color: #045f8c; }

.recommendationCard {
  padding: 10px;
  margin-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-radius: 5px;
  -webkit-transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1) 0s;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1) 0s; }

.recommendationCard:hover {
  opacity: 0.8;
  box-shadow: 5px 5px 10px; }

.burgerMenuItem {
  opacity: 0;
  -webkit-animation: 1s appear forwards;
          animation: 1s appear forwards;
  text-align: center;
  margin-left: -32px; }
  .burgerMenuItem li {
    -webkit-transition: color 0.2s ease-in-out;
    transition: color 0.2s ease-in-out;
    -webkit-animation: 0.5s slideIn forwards;
            animation: 0.5s slideIn forwards; }
    .burgerMenuItem li a {
      text-transform: uppercase;
      color: #e1e1e1;
      cursor: pointer;
      text-decoration: none;
      line-height: 40px;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 2px; }
    .burgerMenuItem li a:hover {
      color: #e17b77; }

.title {
  font-family: "Open Sans", sans-serif;
  font-size: 54px;
  font-weight: 300;
  line-height: 59.4px;
  text-align: center;
  color: #22a39f;
  margin: 25px 0 20px 0;
  display: block; }

.subtitle {
  font-family: "Open Sans", sans-serif;
  font-size: 28px;
  line-height: 30.8px;
  font-weight: 300;
  text-align: center;
  color: #22a39f;
  display: block;
  margin: 20px 0 20px 0; }

.hr {
  margin: 40px 0px;
  border-color: #727878;
  opacity: 0.2; }

@-webkit-keyframes appear {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes appear {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes slideIn {
  0% {
    -webkit-transform: translateX(-2%);
            transform: translateX(-2%); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@keyframes slideIn {
  0% {
    -webkit-transform: translateX(-2%);
            transform: translateX(-2%); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@-webkit-keyframes shrink {
  0% {
    width: 95%; }
  100% {
    width: 90%; } }

@keyframes shrink {
  0% {
    width: 95%; }
  100% {
    width: 90%; } }

